const normalizeOrigin = (origin, dir = 'ltr') =>
	origin
		.replace(/-left$/, dir === 'ltr' ? '-start' : '-end')
		.replace(/-right$/, dir === 'ltr' ? '-end' : '-start')
		.split('-');

const toMatchingOrigin = ([vertical, horizontal]) => {
	// If vertical is center, flip the horizontal position
	if (vertical === 'center') {
		return horizontal === 'start' ? ['center', 'end'] : ['center', 'start'];
	}
	// Otherwise flip the vertical position
	return vertical === 'top' ? ['bottom', horizontal] : ['top', horizontal];
};

const RE_POSITION_LONG = /^(top|bottom|center)-(left|right|start|end|center) (top|bottom|center)-(left|right|start|end|center)$/;
const RE_POSITION_SHORT = /^(top|bottom|center)-(left|right|start|end|center)$/;

/**
 * Normalizes a position based on the following rules:
 * - if a position shorthand is provided, expands it to the full position (e.g. "bottom-start" > "bottom-start top-start")
 * - normalizes direction-specific inline properties to logical properties (e.g. "bottom-right top-left" > "bottom-end top-start")
 * - splits the normalized position into two arrays of origins (e.g. "bottom-end top-start" > [["bottom", "end"], ["top", "start"]])
 * @param {string} position - Shorthand position ("bottom-start") or full position ("bottom-right top-left")
 * @return {Array<Origin[]>}
 */
export default function normalizePosition(position) {
	if (RE_POSITION_LONG.test(position)) {
		const [targetOrigin, contentOrigin] = position.split(' ');
		return [normalizeOrigin(targetOrigin), normalizeOrigin(contentOrigin)];
	}
	if (RE_POSITION_SHORT.test(position)) {
		const targetOrigin = normalizeOrigin(position);
		const contentOrigin = toMatchingOrigin(targetOrigin);
		return [targetOrigin, contentOrigin];
	}
	throw new Error(`${position} is not a valid position`);
}
