import {actionTypes} from '@servicenow/ui-core';
import transform from 'lodash/transform';
import findKey from 'lodash/findKey';
import isEmpty from 'lodash/isEmpty';
import isNil from 'lodash/isNil';
import mapValues from 'lodash/mapValues';
import pickBy from 'lodash/pickBy';
import {getBestFitInfo} from '../fit-info/fit-info';

const BEHAVIOR_NAME = 'fit';
const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_BOOTSTRAPPED
} = actionTypes;

/**
 * Create public Seismic property config for each descriptors in the given map
 *
 * @param {Descriptors} descriptors
 */
function createProperties(descriptors) {
	return transform(descriptors, (result, descriptor, internalName) => {
		const publicName = descriptor.alias || internalName;
		result[publicName] = {
			reflect: isNil(descriptor.reflect) ? true : descriptor.reflect
		};
	});
}

const getFitStyle = ({
	opened,
	target,
	content,
	container,
	position,
	offset,
	constrain,
	canScrollVertical,
	canScrollHorizontal
}) => {
	if (!opened || !target || !content) {
		return {};
	}

	const {style} = getBestFitInfo({
		target,
		content,
		container,
		position,
		offset,
		constrain,
		canScrollVertical,
		canScrollHorizontal
	});
	return {...style, visibility: 'visible'};
};

/**
 * Updates the fit behavior opened state for this component and triggers a
 * re-render with new fit styles.
 * @param {Function} dispatch
 * @param {boolean} opened
 */
export function setFitOpened(dispatch, opened) {
	dispatch(() => {
		return {
			type: 'FIT_BEHAVIOR#STATE_SET',
			payload: {opened}
		};
	});
}

/**
 * Updates the fit behavior target reference for this component and triggers a
 * re-render with new fit styles.
 * @param {Function} dispatch
 * @param {HTMLElement} target
 */
export function setFitTarget(dispatch, target) {
	dispatch(() => {
		return {
			type: 'FIT_BEHAVIOR#STATE_SET',
			payload: {target}
		};
	});
}

/**
 * Updates the fit behavior content reference for this component and triggers a
 * re-render with new fit styles.
 * @param {Function} dispatch
 * @param {HTMLElement} content
 */
export function setFitContent(dispatch, content) {
	dispatch(() => {
		return {
			type: 'FIT_BEHAVIOR#STATE_SET',
			payload: {content}
		};
	});
}

/**
 * Updates the fit behavior container reference for this component and triggers
 * a re-render with new fit styles.
 * @param {Function} dispatch
 * @param {HTMLElement} container
 */
export function setFitContainer(dispatch, container) {
	dispatch(() => {
		return {
			type: 'FIT_BEHAVIOR#STATE_SET',
			payload: {container}
		};
	});
}

/**
 * Support the typical Seismic behavior pattern (shared instance) without
 * needing to use the factory function. This instance does not allow exposing
 * any properties, but can use Seismic's `options` configuration just like any
 * other behavior.
 */
export const fitBehavior = createFitBehavior();

/**
 * Factory function to create a fit behavior that can expose custom properties
 * for the consuming component.
 *
 * @typedef {Object} Descriptor
 * @property {boolean} [public=false] - Whether this option can be exposed
 * as a component property
 * @property {string} [alias] - What property name to use when exposing the
 * option as a property. If not provided, the property name will match the name
 * of the behavior option. Only applies when `public` is `true`.
 * @property {boolean} [reflect=true] - Whether or not to reflect this option
 * in the component's attributes (same as Seismic's `reflect` flag). Only applies
 * when `public` is `true`
 * @property {any} [default] - The default value to use for the option when
 * the property value is not set (or `public` is `false`)
 *
 * @typedef {Object.<string, Descriptor>} Descriptors - A map of behavior option to
 * descriptor that configures that option.
 *
 * @param {Descriptors} descriptors
 */
export function createFitBehavior(descriptors = {}) {
	const publicDescriptors = pickBy(descriptors, {public: true});
	const defaults = mapValues(descriptors, 'default');

	return {
		name: BEHAVIOR_NAME,
		properties: createProperties(publicDescriptors),

		// options should only be provided when using Seismic's
		// normal option passing (no factory function)
		initialState(options) {
			const {
				opened = false,
				target = null,
				content = null,
				container,
				position,
				offset,
				constrain,
				canScrollVertical = false,
				canScrollHorizontal = false
			} = {
				...defaults,
				...options
			};

			return {
				style: {},
				opened,
				target,
				content,
				container,
				position,
				offset,
				constrain,
				canScrollVertical,
				canScrollHorizontal
			};
		},

		actionHandlers: {
			'FIT_BEHAVIOR#STATE_SET': ({action, updateState}) => {
				action.stopPropagation();

				updateState(({state}) => {
					const nextState = {
						...state.behaviors[BEHAVIOR_NAME],
						...action.payload
					};
					const {
						opened,
						target,
						content,
						container,
						position,
						offset,
						constrain,
						canScrollVertical,
						canScrollHorizontal
					} = nextState;
					const style = getFitStyle({
						opened,
						target,
						content,
						container,
						position,
						offset,
						constrain,
						canScrollVertical,
						canScrollHorizontal
					});
					return {
						operation: 'set',
						path: `behaviors.${BEHAVIOR_NAME}`,
						value: {
							...nextState,
							style
						}
					};
				});
			},

			[COMPONENT_BOOTSTRAPPED]: () => {
				// eslint-disable-next-line no-undef
				if (process.env.NODE_ENV === 'development') {
					// eslint-disable-next-line no-console
					console.warn(
						'[@servicenow/behavior-fit]: The fit behavior is deprecated. It is being replaced with the `now-popover` component.'
					);
				}
			},

			[COMPONENT_PROPERTY_CHANGED]: ({action, dispatch}) => {
				const {name, value} = action.payload;
				const key = findKey(publicDescriptors, (descriptor, internalName) => {
					return descriptor.alias === name || internalName === name;
				});

				if (key) {
					dispatch(() => {
						return {
							type: 'FIT_BEHAVIOR#STATE_SET',
							payload: {[key]: value}
						};
					});
				}
			},

			[COMPONENT_DOM_READY]: ({properties, dispatch}) => {
				// update state with the initial values of public properties
				const nextState = transform(
					publicDescriptors,
					(result, descriptor, internalName) => {
						const publicName = descriptor.alias || internalName;
						if (
							properties[publicName] !== undefined &&
							properties[publicName] !== defaults[internalName]
						) {
							result[internalName] = properties[publicName];
						}
					}
				);

				if (!isEmpty(nextState)) {
					dispatch(() => {
						return {
							type: 'FIT_BEHAVIOR#STATE_SET',
							payload: nextState
						};
					});
				}
			}
		}
	};
}
