import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-avatar.scss';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {localizePresence} from '@servicenow/now-icon';
import {t} from '@servicenow/library-translate';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const _getLabel = (userName, presence) => {
	if (presence) {
		const localizedPresence = localizePresence(presence);
		return userName
			? `${userName}, ${localizedPresence}`
			: `${t('Unknown User')}, ${localizedPresence}`;
	} else {
		return userName ? userName : t('Unknown User');
	}
};

const _getAriaLabel = (userName, presence) => {
	return t('User Avatar: {0}', _getLabel(userName, presence));
};

const _getInitials = (userName) => {
	if (!userName) {
		return;
	}
	let names = userName.split(' ');
	if (names.length === 1) {
		return (
			<span className="now-avatar-label">
				<span className="now-line-height-crop">
					{names[0].substring(0, 1).toUpperCase()}
				</span>
			</span>
		);
	} else {
		return (
			<span className="now-avatar-label">
				<span className="now-line-height-crop">
					{names[0].substring(0, 1).toUpperCase() +
						names[1].substring(0, 1).toUpperCase()}
				</span>
			</span>
		);
	}
};

const _renderContent = (
	imageSrc,
	failedImageSrc,
	userName,
	size,
	presence,
	updateState
) => {
	// Display avatar image if provided
	if (imageSrc && imageSrc !== failedImageSrc) {
		return (
			<img
				className="now-avatar-img"
				src={imageSrc}
				alt={_getAriaLabel(userName, presence)}
				on-error={() => {
					updateState({failedImageSrc: imageSrc});
				}}
			/>
		);
	}
	// Fallback to displaying user's initials
	else if (userName) {
		return (
			<span role="img" aria-label={_getAriaLabel(userName, presence)}>
				{_getInitials(userName)}
			</span>
		);
	}
	// Otherwise display the "anonymous" user icon
	else {
		return (
			<now-icon
				className="now-avatar-icon"
				icon="user-fill"
				// avatar sizes and icon sizes don't map directly
				size={size === 'lg' ? 'xl' : size}
				role="img"
				aria-label={_getAriaLabel(userName, presence)}
			/>
		);
	}
};

const _renderPresence = (size, presence) => {
	return presence ? (
		<now-icon-presence
			className="now-avatar-presence-indicator"
			// xl avatar calls for 48px presence (24px * scale(2) in css)
			size={size === 'xl' ? 'lg' : size}
			presence={presence}
			aria-hidden="true"
		/>
	) : (
		undefined
	);
};

const _renderTooltip = (hideTooltip, targetRef, tooltipContent) => {
	if (tooltipContent && !hideTooltip) {
		return (
			<now-tooltip
				id="tooltip"
				target-ref={targetRef}
				content={tooltipContent}
			/>
		);
	}
};

const view = (state, {updateState}) => {
	const {size, userName, imageSrc, presence, hideTooltip} = state.properties;
	const {failedImageSrc, targetRef} = state;

	const setTargetRef = (ref) => setTimeout(() => updateState({targetRef: ref}));

	return (
		<Fragment>
			<span
				class={{
					'now-avatar': true,
					['-' + size]: size,
					'has-image': imageSrc
				}}
				hook-insert={({elm}) => setTargetRef(elm)}
				hook-destroy={() => setTargetRef(null)}>
				{_renderContent(
					imageSrc,
					failedImageSrc,
					userName,
					size,
					presence,
					updateState
				)}
				{_renderPresence(size, presence)}
			</span>
			{_renderTooltip(hideTooltip, targetRef, _getLabel(userName, presence))}
		</Fragment>
	);
};

/**
 * Avatars provide a graphic representation of an identity, displayed as an
 * image, text, or anonymous icon. You can associate an avatar to an identity.
 * For example, you can add a user's avatar to a card to associate that card with the
 * user it is assigned to.
 *
 * ```jsx
 * <now-avatar size="sm"></now-avatar>
 * <now-avatar size="md" user-name="Fred Luddy" presence="busy"></now-avatar>
 * <now-avatar size="lg" image-src="http://myImage.png"></now-avatar>
 * <now-avatar size="xl" user-name="Fred Luddy" hide-tooltip></now-avatar>
 * ```
 *
 * @seismicElement now-avatar
 * @summary Avatar that represents a user by their initials, profile image,
 * or an anonymous icon.
 */
createEnhancedElement('now-avatar', {
	properties: {
		/**
		 * Size of the avatar.
		 * @type {('sm'|'md'|'lg'|'xl')}
		 */
		size: {
			default: 'md',
			schema: {type: 'string', enum: ['sm', 'md', 'lg', 'xl']}
		},
		/**
		 * Name of the user that the avatar represents. If no `imageSrc`
		 * is provided, then the initials of the first two names that are
		 * provided in the string appear in the avatar. The name is also read
		 * aloud to screen reader users. If no name is provided, the screen
		 * reader will announce the avatar user as "unknown user".
		 * @type {string}
		 */
		userName: {schema: {type: 'string'}},
		/**
		 * Location of the user image to display in the avatar.
		 * @type {string}
		 */
		imageSrc: {schema: {type: 'string'}},
		/**
		 * When true, screen readers do not announce the avatar component.
		 * @type {string}
		 */
		ariaHidden: {
			default: 'true',
			reflect: true,
			schema: {type: 'string', enum: ['true', 'false']}
		},
		/**
		 * The user's presence status, displayed with an indicator icon in the corner
		 * of the avatar.
		 * @type {('available'|'busy'|'away'|'offline')}
		 */
		presence: {
			schema: {type: 'string', enum: ['available', 'busy', 'away', 'offline']}
		},
		/**
		 * When true, removes the visual tooltip that displays the full
		 * `userName` value on hover.
		 * @type {boolean}
		 */
		hideTooltip: {default: false, schema: {type: 'boolean'}}
	},
	initialState: {
		failedImageSrc: null,
		targetRef: null
	},
	view,
	styles
});
