import isFunction from 'lodash/isFunction';
import {getActiveElement} from '@servicenow/library-enhanced-utils';
import {actionTypes} from '@servicenow/ui-core';
import {
	handleFocusFirstNode,
	focusFirstNodeSync
} from './utils/handle-focus-first-node';
import {handleTabKeydown} from './utils/handle-tab-keydown';
import {trapEnabled} from './utils/trap-enabled';

const BEHAVIOR_NAME = 'focus';
const {
	COMPONENT_CONNECTED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

const handleOpened = async (host, state, updateState, dispatch) => {
	const triggerEl = getActiveElement();
	if (state.triggerEl !== triggerEl) {
		updateState({
			operation: 'set',
			path: 'triggerEl',
			value: triggerEl,
			shouldRender: false
		});
	}

	handleFocusFirstNode(host, dispatch);
};

const handleClosed = async (triggerEl) => {
	if (!triggerEl) return;
	triggerEl.focus();
};

/**
 * This behavior replaces the native `.focus()` method for the web component's
 * host element in order to allow delegation of focus to internal nodes.
 * By default, `.focus()` will delegate the event to the component's first
 * focusable node.
 *
 * Optionally, you can pass an object to the `options` key for the behavior to
 * further configure focus. See the README for more details. The accepted keys
 * are `focus` and `trap.
 *
 * After installation, import the behavior at the top of your component JS file
 * and register it in your component behaviors list:
 *
 * ```js
 * import focusBehavior from '@servicenow/behavior-focus';
 *
 * createCustomElement('sn-example', {
 *   // ...
 *   behaviors: [{
 *     behavior: focusBehavior,
 *     options: {
 *       focus: function(host, dispatch) {
 *         // do something when .focus() is called on this component
 *       },
 *       // optionally add focus trapping
 *       trap: true
 *     }
 *   }]
 * });
 * ```
 *
 * @seismicBehavior focusBehavior
 * @summary Manages focus delegation for a web component
 */
export default {
	name: BEHAVIOR_NAME,
	eventHandlers: [{events: ['keydown'], effect: handleTabKeydown}],
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({
			action,
			state,
			properties: {opened},
			dispatch,
			host,
			updateState
		}) => {
			const {options} = action.payload;

			host.focus = () => {
				return isFunction(options.focus)
					? options.focus(host, dispatch)
					: handleFocusFirstNode(host, dispatch);
			};

			if (options.trap) {
				// Set initial opened focus state if
				// the dialog is initially opened
				if (opened) {
					handleOpened(host, state, updateState, dispatch);
				}

				const path = `behaviors.${BEHAVIOR_NAME}`;

				// TODO-SEISMIC: Remove this once behavior options become
				// available to event handlers in a future version.
				// Issue: DEF0095619
				updateState({
					path,
					operation: 'set',
					value: options,
					shouldRender: false
				});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			host,
			dispatch,
			updateState,
			state
		}) => {
			if (!trapEnabled(state)) return;
			const {name, value} = action.payload;

			if (name === 'opened') {
				return value
					? handleOpened(host, state, updateState, dispatch)
					: handleClosed(state.triggerEl);
			}
		},
		[COMPONENT_DISCONNECTED]: ({state}) => {
			if (!trapEnabled(state)) return;
			handleClosed(state.triggerEl);
		},
		'PRIVATE#FOCUS_BEHAVIOR_FOCUS_FIRST_NODE': ({action, host}) => {
			action.stopPropagation();
			// This action only ever gets called if the component
			// is hoisted, however, we'll fall back to the original host
			// just in case the component was unhoisted before this
			// action executes.
			focusFirstNodeSync(action.meta.hoistHost || host);
		}
	}
};
