import {getFocusableNodes} from './get-focusable-nodes';
import {trapEnabled} from './trap-enabled';

/**
 * Effect callback in a `keydown` event handler.
 *
 * Needs to be used in `eventHandlers`:
 *
 * ```js
 * createCustomElement('my-tag', {
 * 	eventHandlers: [
 * 		{
 * 			events: 'keydown',
 * 			effect: handleTabKeyDown
 * 		}
 * 	],
 * 	// ...rest of config
 * });
 * ```
 *
 * @param {Object} coeffects - Callback in a `keydown` event handler.
 */
export const handleTabKeydown = ({action, host, state}) => {
	const {
		meta: {hoistHost},
		payload: {event}
	} = action;

	if (!trapEnabled(state) || event.key !== 'Tab' || !host.opened) return;

	const path = event.composedPath();
	const {firstFocusableElement, lastFocusableElement} = getFocusableNodes(
		hoistHost || host
	);

	if (path[0] !== firstFocusableElement && path[0] !== lastFocusableElement)
		return;

	if (event.shiftKey) {
		if (path[0] === firstFocusableElement) {
			lastFocusableElement.focus();
			action.preventDefault();
		}
	} else {
		if (path[0] === lastFocusableElement) {
			firstFocusableElement.focus();
			action.preventDefault();
		}
	}
};
