const actionSchema = {
	type: 'object',
	properties: {
		icon: {type: 'string'},
		label: {type: 'string'},
		clickActionType: {type: 'string'},
		mouseenterActionType: {type: 'string'},
		mouseleaveActionType: {type: 'string'}
	}
};

const highlightedValueSchema = {
	type: 'object',
	properties: {
		label: {type: 'string'},
		status: {type: 'string'},
		width: {type: 'string'}
	}
};

const identifierSchema = {
	type: 'object',
	properties: {
		type: {type: 'string', enum: ['icon', 'avatar']},
		icon: {type: 'string'},
		userName: {type: 'string'},
		imageSrc: {type: 'string'},
		ariaHidden: {type: 'string', enum: ['true', 'false']}
	}
};

export const itemsSchema = {
	$id: 'itemsSchema',
	type: 'array',
	items: {
		type: 'object',
		properties: {
			id: {type: 'string'},
			label: {type: 'string'},
			children: {type: 'array', items: {$ref: 'itemsSchema#/items'}},
			childrenAvailable: {type: 'number'},
			identifierProps: identifierSchema,
			actionable: {type: 'boolean'},
			actions: {type: 'array', items: actionSchema},
			highlightedValueProps: {type: 'array', items: highlightedValueSchema},
			disabled: {type: 'boolean'}
		}
	}
};
