import {actionTypes} from '@servicenow/ui-core';
import ResizeObserver from 'resize-observer-polyfill';
import debounce from 'lodash/debounce';
import throttle from 'lodash/throttle';
import noop from 'lodash/noop';

const {COMPONENT_CONNECTED, COMPONENT_DISCONNECTED} = actionTypes;
const BEHAVIOR_NAME = 'resize';
const observedMap = new WeakMap();

const resize = new ResizeObserver((entries) => {
	window.requestAnimationFrame(() => {
		for (let entry of entries) {
			const {
				target,
				contentRect: {width, height}
			} = entry;
			(observedMap.get(target) || noop)({width, height});
		}
	});
});

const getTimerFn = (type) => {
	switch (type) {
		case 'throttle':
			return throttle;
		case 'debounce':
		default:
			return debounce;
	}
};

/**
 * This behavior measures the height and width of components dynamically and
 * adds those measurements to state. This helps components that need to know
 * their size to determine what to show/hide, and need to update dynamically.
 *
 * You probably don't need this behavior. See **This should be your last
 * resort** for details.
 *
 * See the **How it works** section below for restrictions and performance
 * details.
 *
 * **Register behavior**
 *
 * After installation import the behavior at the top of your component JS file
 * and register it in your component behaviors list:
 *
 * ```js
 * import resizeBehavior from '@servicenow/behavior-resize';
 *
 * createCustomElement('sn-example', {
 *   renderer: {
 *     type: snabbdom,
 *     view: (state) => {
 *       const {width, height} = state.behaviors.resize.size;
 *       // These will be `null` initially. When the component first attaches it'll
 *       // re-render with integer values in pixels (e.g. `width === 100`).
 *       // When the component's width is changed it'll re-render again with the
 *       // new integer values.
 *     }
 *   },
 *   behaviors: [{behavior: resizeBehavior}]
 * });
 * ```
 *
 * See `example/sn-example-resize.js` for a more complete example.
 *
 * **Configuration options**
 *
 * The following options can be used to configure the behavior
 *
 * - `[timer={}] (Object)` - configures the timer used debounce/throttle invocation of the resize observer
 * - `[timer.type="debounce"] (string)` - the type of timer to use, "debounce" or "throttle"
 * - `[timer.wait=20] (number)` - milliseconds to delay invocation of observer
 *
 * Configuration example:
 *
 * ```js
 * import resizeBehavior from '@servicenow/behavior-resize';
 *
 * createCustomElement('sn-foo', {
 *   ...
 *   behaviors: [{
 *     behavior: resizeBehavior,
 *     options: {timer: {type: 'throttle', wait: 200}}
 *   }]
 * });
 * ```
 *
 * **How it works**
 *
 * This behavior is based on the [ResizeObserver browser specification](https://www.google.com/search?q=resizeobserver&oq=resizeobserver&aqs=chrome.0.69i59l2j0l2j69i61j69i60.3313j0j7&sourceid=chrome&ie=UTF-8).
 * The behavior will use the browser's native ResizeObserver if the browser has
 * that feature.
 *
 * For older browsers, it falls back to using MutationObserver and dirty
 * checking to react to component size changes. It currently uses the
 * [resize-observer-polyfill](https://github.com/que-etc/resize-observer-polyfill)
 * for fallback, but we may change that out to improve performance in the
 * future. This is unlikely to break the API but may lead to slight timing
 * differences for older browsers only.
 *
 * **This should be your last resort**
 *
 * Measuring things is expensive. You probably don't need to use this behavior
 * to measure things. Try every other recourse before you use this behavior. Use
 * CSS media queries instead. Or use CSS flex/truncation behaviors to closely
 * simulate what you're trying to do. No matter how performant we try to make
 * this behavior, it'll always cause a performance hit when a lot of elements on
 * the page use it.
 *
 * **Component requirements**
 *
 * To use this behavior your component MUST meet the following criteria:
 *
 * - Its CSS `display` value can't be `inline` or `inline-*`. Use a non-inline
 * value like `block` or `flex`.
 * - It can't change its size dynamically based on its measured width/height if
 * in the flow of the document. When it renders in response to a width/height
 * change it can't change its size, otherwise it'll go into an infinite loop.
 * For example, if you want to truncate text based on the width of the
 * component, you should set the text container to a non-auto width and height
 * to ensure that the truncation doesn't cause a resize, which triggers another
 * truncation.
 * - It's nice if the component isn't usually stretched across the full viewport
 * width. If many components stretched across the full viewport width use this
 * behavior it can lead to jank and dropped frames when the user resizes the
 * browser or animates a page out of visibility.
 *
 * @seismicBehavior resizeBehavior
 * @summary Measures the height/width of components and adds values to state
 */
export default {
	name: BEHAVIOR_NAME,
	initialState: {
		size: {
			width: null,
			height: null
		}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({dispatch, action: {payload}}) => {
			const {host, options} = payload;
			const {type = 'debounce', wait = 300} = options.timer || {};
			const timerFn = getTimerFn(type);
			const fn = timerFn(({width, height}) => {
				dispatch(() => {
					return {
						type: 'NOW_RESIZE#ELEMENT_RESIZED',
						payload: {width, height}
					};
				});
			}, wait);
			resize.observe(host);
			observedMap.set(host, fn);
		},
		[COMPONENT_DISCONNECTED]: ({action: {payload}}) => {
			resize.unobserve(payload.host);
			observedMap.delete(payload.host);
		},
		'NOW_RESIZE#ELEMENT_RESIZED': ({updateState, action}) => {
			action.stopPropagation();
			const {width, height} = action.payload;

			updateState(() => {
				return {
					operation: 'set',
					path: `behaviors.${BEHAVIOR_NAME}.size`,
					value: {width, height}
				};
			});
		}
	}
};
