/**
 * Caches the result of a `getDimensions()` call so that it won't be
 * recalculated again later. Useful to ensure that getBoundingClientRect is
 * not called multiple times in a row when multiple fit positions are
 * evaluated in a row.
 * @param {Dimensions} dim
 * @return {{getDimensions: Function}}
 */
export function cacheDimensions(dim) {
	return {
		getDimensions: () => dim
	};
}

/**
 * Gets the dimensions of a HTMLElement or gets the manually specified and/or
 * cached dimensions of a DimensionsReference.
 * @param {(HTMLElement|DimensionsReference)} node
 * @return {Dimensions}
 */
export function getDimensions(node) {
	if (typeof node.getDimensions === 'function') {
		return node.getDimensions();
	}
	if (node === window || node === document.documentElement) {
		return {
			top: 0,
			right: window.innerWidth,
			bottom: window.innerHeight,
			left: 0,
			width: window.innerWidth,
			height: window.innerHeight,
			margin: {top: 0, right: 0, bottom: 0, left: 0}
		};
	}
	const {
		top,
		right,
		bottom,
		left,
		width,
		height
	} = node.getBoundingClientRect();
	const style = window.getComputedStyle(node);
	return {
		top,
		right,
		bottom,
		left,
		width,
		height,
		margin: {
			top: parseInt(style.marginTop, 10) || 0,
			right: parseInt(style.marginRight, 10) || 0,
			bottom: parseInt(style.marginBottom, 10) || 0,
			left: parseInt(style.marginLeft, 10) || 0
		}
	};
}

const CONTENT_DIMENSION_DEFAULTS = {
	width: 'auto',
	minWidth: 0,
	maxWidth: Infinity,
	height: 'auto',
	minHeight: 0,
	maxHeight: Infinity
};

const toSizeValue = (contentDimensions, targetDimensions, size, value) => {
	if (value === 'auto') {
		return contentDimensions[size];
	}
	if (value === 'target') {
		return targetDimensions[size];
	}
	if (typeof value === 'number') {
		return value;
	}
	throw new Error('Sizing value is not valid');
};

/**
 * Constrains content dimensions based on developer-specified configuration.
 * @param {Dimensions} contentDimensions
 * @param {Dimensions} targetDimensions
 * @param {Object} constraints
 * @return {Dimensions}
 */
export function getContentDimensions(
	contentDimensions,
	targetDimensions,
	constraints,
	canScrollVertical,
	canScrollHorizontal
) {
	const wval = (v) =>
		toSizeValue(contentDimensions, targetDimensions, 'width', v);
	const hval = (v) =>
		toSizeValue(contentDimensions, targetDimensions, 'height', v);
	const {width, minWidth, maxWidth, height, minHeight, maxHeight} = {
		...CONTENT_DIMENSION_DEFAULTS,
		...constraints
	};
	const vScroll =
		canScrollVertical &&
		Math.max(hval(height), hval(minHeight)) > hval(maxHeight)
			? 17
			: 0;
	const hScroll =
		canScrollHorizontal &&
		Math.max(wval(width), wval(minWidth)) > wval(maxWidth)
			? 17
			: 0;
	return {
		...contentDimensions,
		width: Math.min(
			Math.max(wval(width) + vScroll, wval(minWidth)),
			wval(maxWidth)
		),
		height: Math.min(
			Math.max(hval(height) + hScroll, hval(minHeight)),
			hval(maxHeight)
		)
	};
}
