import {findTabbableNodes} from '@servicenow/library-enhanced-utils';

/**
 * Returns an object whose properties include relevant focusable elements.
 * @param {HTMLElement} host
 * @returns {{tabbableNodes: HTMLElement[], firstFocusableElement: HTMLElement, lastFocusableElement: HTMLElement}}
 */
export const getFocusableNodes = (host) => {
	const tabbableNodes = findTabbableNodes(host);
	const firstFocusableElement = tabbableNodes[0];
	const lastFocusableElement = tabbableNodes[tabbableNodes.length - 1];

	return {
		tabbableNodes,
		firstFocusableElement,
		lastFocusableElement
	};
};
