import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {createElementFromNode} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-icon.scss';
import {
	presenceFill12,
	presenceFill16,
	presenceFill24,
	presenceFill32,
	presenceAwayFill12,
	presenceAwayFill16,
	presenceAwayFill24,
	presenceAwayFill32,
	presenceAvailableFill12,
	presenceAvailableFill16,
	presenceAvailableFill24,
	presenceAvailableFill32,
	presenceBusyFill12,
	presenceBusyFill16,
	presenceBusyFill24,
	presenceBusyFill32,
	presenceOfflineFill12,
	presenceOfflineFill16,
	presenceOfflineFill24,
	presenceOfflineFill32
} from './now-icon-set';
const icons = {
	background12:
		'<g stroke="none" fill="none"><path d="M2 6.5a4.5 4.5 0 1 0 9 0 4.5 4.5 0 0 0-9 0z" fill="white"></path></g>',
	background16:
		'<g stroke="none" fill="none"><path d="M3 8.5a5.5 5.5 0 1 0 11 0 5.5 5.5 0 0 0-11 0z" fill="white"></path></g>',
	background24:
		'<g stroke="none" fill="none"><path d="M5 12.5a7.5 7.5 0 1 0 15 0 7.5 7.5 0 0 0-15 0z" fill="white"></path></g>',
	background32:
		'<g stroke="none" fill="none"><path d="M8 16.5a8.5 8.5 0 1 0 17 0 8.5 8.5 0 0 0-17 0z" fill="white"></path></g>',
	presenceFill12,
	presenceFill16,
	presenceFill24,
	presenceFill32,
	presenceAwayFill12,
	presenceAwayFill16,
	presenceAwayFill24,
	presenceAwayFill32,
	presenceAvailableFill12,
	presenceAvailableFill16,
	presenceAvailableFill24,
	presenceAvailableFill32,
	presenceBusyFill12,
	presenceBusyFill16,
	presenceBusyFill24,
	presenceBusyFill32,
	presenceOfflineFill12,
	presenceOfflineFill16,
	presenceOfflineFill24,
	presenceOfflineFill32
};
import localizePresence from './utils/localize-presence';

// Safe to assume this is the base for now. If base font size has the potential to change in the future, we'll have to make this dynamic somehow.
const BASE_FONT_SIZE = 16;
const ICON_SIZES = {
	sm: 12,
	md: 16,
	lg: 24,
	xl: 32
};

const generateIcon = (state) => {
	const {size, presence, accessibilityMode} = state.properties;
	const iconSize = ICON_SIZES[size];
	const iconRems = iconSize / BASE_FONT_SIZE;
	const iconName = accessibilityMode
		? `presence${presence.charAt(0).toUpperCase() +
				presence.slice(1)}Fill${iconSize}`
		: `presenceFill${iconSize}`;
	const wrapper = document.createElement('SPAN');
	const presenceLabel = localizePresence(presence);
	const background = `background${iconSize}`;

	wrapper.innerHTML = `<svg
	  xmlns="http://www.w3.org/2000/svg"
	  role="img"
      aria-label="${presenceLabel}"
      viewBox="0 0 ${iconSize} ${iconSize}"
	  style="width: ${iconRems}rem; height: ${iconRems}rem;"
	  class="now-icon-presence -${presence}">
		  ${accessibilityMode ? icons[background] : ''}
          ${icons[iconName]}
      </svg>
    `;

	const vnode = createElementFromNode(wrapper);

	return vnode.children[0];
};

const view = (state) => {
	return generateIcon(state);
};

/**
 * Presence indicator icon that shows the status and availability of a user.
 *
 * ```jsx
 * <now-icon-presence presence="available" size="lg"></now-icon-presence>
 *
 * <now-icon-presence presence="busy" accessibility-mode></now-icon-presence>
 * ```
 *
 * Note: Per [WCAG Success Criterion 1.4.1](https://www.w3.org/TR/WCAG21/#use-of-color),
 * use of these icons alone is not an accessible method to communicate a
 * user's online presence. Set the `accessibility-mode` property to render a
 * different glyph shape for each `presence` value. For users of screen readers,
 * an 'aria-label' is provided with this component by default. A utility method
 * called `localizePresence` is also available. This method can be used to provide an
 * internationalized version of the `presence` value in a tooltip or
 * other supplemental aids for users with limited color vision.
 *
 * ```js
 * import {localizePresence} from '@servicenow/now-icon';
 * import {t} from '@servicenow/library-translate';
 *
 * const a11yLabel = t('User Status: {0}', localizePresence(presence));
 * ```
 *
 * @seismicElement now-icon-presence
 * @summary Presence indicator icon used to show availability of a user.
 */
createEnhancedElement('now-icon-presence', {
	properties: {
		/**
		 * Presence of the user that drives the visual display of the icon.
		 * @type {('available'|'busy'|'away'|'offline')}
		 */
		presence: {},
		/**
		 * Size of the icon.
		 * @type {('sm'|'md'|'lg'|'xl')}
		 */
		size: {default: 'md'},
		/**
		 * Set this flag to use accessible presence icons, where different
		 * icon glyphs represent different `presence` values.
		 * @type {boolean}
		 */
		accessibilityMode: {default: false}
	},
	view,
	styles
});
