import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {measureTextFactory} from '@servicenow/library-enhanced-utils';
import {actionTypes} from '@servicenow/ui-core';
const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
	COMPONENT_RENDERED
} = actionTypes;
import styles from './_now-tabs.scss';
import resizeBehavior from '@servicenow/behavior-resize';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-dropdown';
import '@servicenow/now-icon';
import {localizePresence} from '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {t} from '@servicenow/library-translate';
import sum from 'lodash/sum';
import {itemSchema} from './schemas/tabs-schema';

const TOOLTIP_DELAY = 300;
const timeoutMap = new Map();

// TODO-SEISMIC-HACK: Remove if we can switch to a scheduled/cancellable action
// instead of implementing our own approach
const schedule = (key, ms, fn) => {
	window.clearTimeout(timeoutMap.get(key));
	const newTimeout = window.setTimeout(fn, ms);
	timeoutMap.set(key, newTimeout);
};

const unschedule = (key) => {
	if (timeoutMap.has(key)) {
		window.clearTimeout(timeoutMap.get(key));
	}
};

const mt = measureTextFactory('16px Source Sans Pro,sans-serif');
const measureText = (text) => {
	return Math.ceil(mt(text));
};

const PADDING = 16 * 2;

const measureTabItems = (items, fixedWidth, hideLabel, maxWidth, ICON_SIZE) => {
	if (fixedWidth) {
		return Array(items.length).fill(hideLabel ? 48 : 100);
	}
	return items.map((item) => {
		let width = PADDING;
		width += item.icon || item.presence ? ICON_SIZE : 0;
		width += hideLabel ? 0 : measureText(item.label);
		width += item.count
			? measureText(' (') + measureText(item.count) + measureText(')')
			: 0;
		return Math.min(maxWidth, width);
	});
};

const measureTabs = (
	widthAvailable,
	items,
	hideLabel,
	fixedWidth,
	selectedId,
	maxWidth
) => {
	// only proceed if resize behavior has provided us total width available
	if (!widthAvailable) {
		return {visibleItems: items, overflowItems: [], selectedInOverflow: false};
	}

	const ICON_SIZE = hideLabel ? 16 : 16 + 2;

	// create array with width required for each tab
	const tabWidths = measureTabItems(
		items,
		fixedWidth,
		hideLabel,
		maxWidth,
		ICON_SIZE
	);
	let widthRequired = sum(tabWidths);

	let i = items.length;
	let overflowMenuWidth;
	const selectedItemIndex = items.findIndex((item) => item.id === selectedId);

	while (widthRequired > widthAvailable && i > 0) {
		i--;
		const longestLabel = hideLabel
			? // dropdown needs space for ellipsis or longest icon+data
			  Math.max(...tabWidths.slice(i), PADDING + ICON_SIZE)
			: // dropdown needs space for "More" or longest icon+label+data
			  Math.max(...tabWidths.slice(i), PADDING + measureText(t('More')));
		// include the dropdown caret
		overflowMenuWidth = Math.min(ICON_SIZE + longestLabel, maxWidth);
		// need space for overflow menu + all remaining non-overflowed tabs
		widthRequired = overflowMenuWidth + sum(tabWidths.slice(0, i));
	}
	return {
		visibleItems: items.slice(0, i),
		overflowItems: items.slice(i),
		selectedInOverflow: selectedItemIndex >= i
	};
};

const selectTab = (host, index, properties, dispatch, updateState) => {
	const {items, selectedItem, manageSelected, hideLabel} = properties;
	const newSelectedItem = items.filter((item) => !item.disabled)[index];
	if (selectedItem !== newSelectedItem.id && !manageSelected) {
		dispatch(() => {
			return {
				type: 'NOW_TABS#SELECTED_ITEM_SET',
				payload: {
					value: newSelectedItem.id
				}
			};
		});
		let tabs = host.shadowRoot.querySelectorAll(
			'button.now-tab:not(.is-disabled)'
		);
		if (index > tabs.length - 1) {
			let dropdown = host.shadowRoot.querySelector('now-dropdown');
			dropdown.selectedItems = [newSelectedItem.id];
			updateState({focusDropdown: true, shouldRender: false});
		} else {
			tabs[index].focus();
			if (hideLabel) {
				showTooltip(updateState, tabs[index], newSelectedItem.label);
			}
			updateState({focusDropdown: false, shouldRender: false});
		}
	}
};

const showTooltip = (updateState, tab, label, hideLabel) => {
	const labelSpan = tab.querySelector('.now-tab-label');
	if (hideLabel || labelSpan.scrollWidth > labelSpan.clientWidth) {
		updateState({
			path: 'currentTooltip',
			operation: 'set',
			value: {ref: tab, content: label, opened: true}
		});
	}
};

const hideTooltip = (updateState) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: null, content: '', opened: false}
	});
};

const getDropdownOpened = (host) => {
	const dropdown = host.shadowRoot.querySelector('now-dropdown');
	if (dropdown && dropdown.opened) {
		return true;
	}
	return false;
};

const renderTabIcon = ({item, hideLabel, selected}) => {
	if (item.icon) {
		return (
			<now-icon
				class={{'now-m-inline-end--xxs': !hideLabel}}
				icon={
					selected
						? item.icon.replace('outline', 'fill')
						: item.icon.replace('fill', 'outline')
				}
			/>
		);
	}
	if (item.presence) {
		return (
			<now-icon-presence
				className="now-tab-presence now-m-inline-end--xxs"
				presence={item.presence}
				aria-hidden="true"
			/>
		);
	}
	return '';
};

const renderTab = ({
	key,
	item,
	hideLabel,
	selected,
	fixedWidth,
	maxWidth,
	disabled,
	dispatch,
	updateState,
	componentId
}) => {
	const count = item.count ? `\u00A0(${item.count})` : '';
	const label = hideLabel ? undefined : item.label;

	const ariaLabel = item.presence
		? `${item.label}${count}, ${localizePresence(item.presence)}`
		: `${item.label}${count}`;

	return (
		<button
			key={key}
			class={{
				'now-tab': true,
				'is-selected': selected,
				'is-disabled': disabled
			}}
			style={{'max-width': fixedWidth ? undefined : maxWidth + 'px'}}
			role="tab"
			tabIndex={selected ? undefined : '-1'}
			disabled={disabled}
			aria-selected={selected ? 'true' : 'false'}
			aria-label={ariaLabel}
			on-click={
				selected || disabled
					? undefined
					: () => {
							dispatch(() => {
								return {
									type: 'NOW_TABS#SELECTED_ITEM_SET',
									payload: {value: key}
								};
							});
					  }
			}
			on-mouseenter={(e) => {
				const tab = e.target;
				schedule(componentId, TOOLTIP_DELAY, () =>
					showTooltip(updateState, tab, item.label, hideLabel)
				);
			}}
			on-focusin={(e) => {
				const tab = e.target;
				schedule(componentId, TOOLTIP_DELAY, () =>
					showTooltip(updateState, tab, item.label, hideLabel)
				);
			}}
			on-mouseleave={() => {
				schedule(componentId, TOOLTIP_DELAY, () => hideTooltip(updateState));
			}}
			on-focusout={() => {
				schedule(componentId, TOOLTIP_DELAY, () => hideTooltip(updateState));
			}}>
			{renderTabIcon({item, hideLabel, selected})}
			<span className="now-line-height-crop">
				<span className="now-align">
					<span className="now-tab-label">{label}</span>
					{count}
				</span>
			</span>
		</button>
	);
};

const view = (state, {dispatch, updateState}) => {
	const {componentId} = state;
	const {
		items,
		selectedItem,
		hideLabel,
		fixedWidth,
		maxWidth
	} = state.properties;

	let {visibleItems, overflowItems, selectedInOverflow} = measureTabs(
		state.behaviors.resize.size.width,
		items,
		hideLabel,
		fixedWidth,
		selectedItem,
		maxWidth
	);

	return (
		<div
			class={{
				'now-tabs': true,
				'fixed-width': fixedWidth,
				'icon-only': hideLabel
			}}
			role="tablist">
			{visibleItems &&
				visibleItems.map((item) => {
					return renderTab({
						key: item.id,
						item,
						hideLabel,
						selected: selectedItem === item.id,
						fixedWidth,
						maxWidth,
						disabled: item.disabled,
						dispatch,
						updateState,
						componentId
					});
				})}
			<now-tooltip
				id="tooltip"
				manage-opened
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
				content={state.currentTooltip.content}
				delay={{show: 0, hide: 0}}
			/>
			{overflowItems.length ? (
				<now-dropdown
					config-aria={{
						'aria-label': selectedInOverflow
							? t(
									'Selected tab: {0}',
									items.find((item) => item.id === selectedItem).label
							  )
							: t('More tabs')
					}}
					class={{
						'now-tab-dropdown': true,
						'is-selected': selectedInOverflow
					}}
					tabindex={selectedInOverflow ? undefined : '-1'}
					variant="inherit"
					placeholder={hideLabel ? undefined : t('More')}
					tooltip-content={
						selectedInOverflow
							? items.find((item) => item.id === selectedItem).label
							: t('More tabs')
					}
					icon={hideLabel ? 'ellipsis-h-fill' : undefined}
					items={overflowItems}
					selected-items={selectedInOverflow ? [selectedItem] : undefined}
					hide-label={hideLabel}
					show-padding
					bare
				/>
			) : (
				undefined
			)}
		</div>
	);
};

/**
 * Tabs provide navigation between grouped content in a specific hierarchy.
 * They also identify a users specific location in the display.
 *
 * ```jsx
 * const items = [
 *  {id: 'tab1', label: 'Tab 1', icon: 'bell-outline', count: 7},
 * 	{id: 'tab2', label: 'Tab 2', icon: 'document-outline', count: 3}
 * ];
 *
 * <now-tabs items={items} hide-label fixed-width />
 * ```
 *
 * @seismicElement now-tabs
 * @summary Horizontal tab menu for navigating between tabbed content.
 */
createEnhancedElement('now-tabs', {
	properties: {
		/**
		 * An array of objects, each with the configuration for a tab.
		 * @type {TabItem[]}
		 */
		items: {required: true, schema: {type: 'array', items: itemSchema}},
		/**
		 * ID of the selected tab item.
		 * @type {string}
		 */
		selectedItem: {manageable: true, schema: {type: 'string'}},
		/**
		 * Set this flag to display tabs with only an icon and no label inside.
		 * You should still specify a label in the `items` array, for displaying
		 * in a tooltip and for representing the tab to assistive technology.
		 * @type {boolean}
		 */
		hideLabel: {default: false, schema: {type: 'boolean'}},
		/**
		 * Set this flag to display each tab with a uniform width, a fraction
		 * of the total width taken up by the component.
		 * @type {boolean}
		 */
		fixedWidth: {default: false, schema: {type: 'boolean'}},
		/**
		 * Maximum width (as a number of pixels) for each tab element.
		 * Only applies for dynamic width tabs, when `fixedWidth` is not true.
		 * @type {number}
		 */
		maxWidth: {default: 240, schema: {type: 'number'}}
	},
	initialState: {
		currentTooltip: {ref: null, content: '', opened: false},
		focusDropdown: false
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({host}) => {
			if (
				host.items &&
				host.items.length &&
				!host.selectedItem &&
				!host.manageSelected &&
				host.items.filter((item) => !item.disabled).length
			) {
				host.selectedItem = host.items.filter((item) => !item.disabled)[0].id;
			}
		},
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		},
		[COMPONENT_RENDERED]: ({state, host, updateState}) => {
			const dropdown = host.shadowRoot.querySelector('now-dropdown');
			if (state.focusDropdown && dropdown) {
				dropdown.focus();
				updateState({focusDropdown: false, shouldRender: false});
			}
		},
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({dispatch, action}) => {
			dispatch(() => {
				return {
					type: 'NOW_TABS#SELECTED_ITEM_SET',
					payload: {
						value: action.payload.value[0]
					}
				};
			});
		},
		'NOW_TOOLTIP#OPENED_SET': ({action, updateState}) => {
			const {value, target} = action.payload;
			const componentId = action.meta.path[action.meta.path.length - 1];
			if (target === 'content' && value) {
				unschedule(componentId);
			}
			if ((target === 'content' || target === 'keyboard') && !value) {
				schedule(componentId, TOOLTIP_DELAY, () => hideTooltip(updateState));
			}
		}
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({action, properties, dispatch, updateState}) => {
				const {host, event} = action.payload;
				if (event.key === 'ArrowRight') {
					event.stopPropagation();
					event.preventDefault();
					if (getDropdownOpened(host)) {
						return false;
					}
					const selectedIndex = host.items
						.filter((item) => !item.disabled)
						.findIndex((item) => item.id === host.selectedItem);
					// wraparound from last to first
					const nextIndex =
						selectedIndex ===
						host.items.filter((item) => !item.disabled).length - 1
							? 0
							: selectedIndex + 1;
					selectTab(host, nextIndex, properties, dispatch, updateState);
				} else if (event.key === 'ArrowLeft') {
					event.stopPropagation();
					event.preventDefault();
					if (getDropdownOpened(host)) {
						return false;
					}
					const selectedIndex = host.items
						.filter((item) => !item.disabled)
						.findIndex((item) => item.id === host.selectedItem);
					// wraparound from first to last
					const previousIndex =
						selectedIndex === 0
							? host.items.filter((item) => !item.disabled).length - 1
							: selectedIndex - 1;
					selectTab(host, previousIndex, properties, dispatch, updateState);
				} else if (event.key === 'Home') {
					event.stopPropagation();
					event.preventDefault();
					const firstIndex = 0;
					selectTab(host, firstIndex, properties, dispatch, updateState);
				} else if (event.key === 'End') {
					event.stopPropagation();
					event.preventDefault();
					const lastIndex =
						host.items.filter((item) => !item.disabled).length - 1;
					selectTab(host, lastIndex, properties, dispatch, updateState);
				}
			}
		}
	],
	behaviors: [
		{
			behavior: resizeBehavior
		},
		{
			behavior: focusBehavior
		}
	],
	view,
	styles,
	dispatches: {
		/**
		 * Action dispatched when a tab is selected. The payload contains
		 * the `id` of the item from the `items` array that was selected by
		 * the user.
		 * @type {{value: string}}
		 */
		'NOW_TABS#SELECTED_ITEM_SET': {}
	}
});
