import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {createElementFromNode} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-icon.scss';
import * as icons from './now-icon-set-with-aliases';

// Safe to assume this is the base for now. If base font size has the potential to change in the future, we'll have to make this dynamic somehow.
const BASE_FONT_SIZE = 16;
const ICON_SIZES = {
	sm: 12,
	md: 16,
	lg: 24,
	xl: 32
};

const camelize = (str) => {
	return str.replace(/^([A-Z])|[\s-_]+(\w)/g, function(match, p1, p2) {
		return p2 ? p2.toUpperCase() : p1.toLowerCase();
	});
};

const generateIcon = (state) => {
	const {icon, size, dir, spin} = state.properties;
	const iconSize = ICON_SIZES[size];
	/**
	 * Fix for issue #890. Need to set the icon size to rems so the icon scales
	 * when font-size is changed globally in the browser. There is no unit test
	 * covering this use case cause no idea how to do that automatically.
	 * To test manually, go into browser settings and change the Font Size.
	 * Icon should scale up/down appropriately.
	 */
	const iconRems = iconSize / BASE_FONT_SIZE;

	let rtlIconName = icon;
	if (icon.includes('-start')) {
		if (dir === 'rtl') {
			rtlIconName = icon.replace('-start', '-right');
		} else {
			rtlIconName = icon.replace('-start', '-left');
		}
	}
	if (icon.includes('-end')) {
		if (dir === 'rtl') {
			rtlIconName = icon.replace('-end', '-left');
		} else {
			rtlIconName = icon.replace('-end', '-right');
		}
	}

	const iconName = camelize(rtlIconName || '') + iconSize;
	if (!iconName || !icons[iconName]) {
		return null;
	} else {
		const wrapper = document.createElement('SPAN');

		wrapper.innerHTML = `<svg
			xmlns="http://www.w3.org/2000/svg"
			aria-hidden="true"
			viewBox="0 0 ${iconSize} ${iconSize}"
			style="width: ${iconRems}rem; height: ${iconRems}rem;"
			${spin ? 'class="-spin"' : undefined}>
				${icons[iconName]}
			</svg>
		`;

		const vnode = createElementFromNode(wrapper);

		return vnode.children[0];
	}
};

const view = (state) => {
	return generateIcon(state);
};

/**
 * An icon is a graphic symbol that represents an object or an item.
 * Icons provide clarity and support for surrounding components while reducing visual noise.
 * Each icon is unique and serves a specific purpose.
 *
 * ```jsx
 * <now-icon icon="address-book-outline" size="md"></now-icon>
 * ```
 *
 * Note: You can horizontally mirror any direction-sensitive icon used
 * with a right-to-left language. Set the parent element (or document)
 * `dir` attribute to "rtl" and replace `-left` or `-right` in
 * the icon name with `-start` and `-end`. If you do not want the
 * icon to mirror horizontally, use the explicit direction in the icon name instead.
 * For example, use `arrow-left-fill` instead of `arrow-start-fill`.
 *
 * ```jsx
 * <now-icon icon="arrow-start-fill" dir="rtl" />
 * ```
 *
 * @seismicElement now-icon
 * @summary Custom icon set for the Now Design System.
 */
createEnhancedElement('now-icon', {
	properties: {
		/**
		 * Name of the icon in dash-case, including the `-fill` or `-outline` suffix to
		 * indicate whether the icon is filled or outlined.
		 * @type {string}
		 */
		icon: {required: true, schema: {type: 'string'}},
		/**
		 * Size of the icon.
		 * @type {('sm'|'md'|'lg'|'xl')}
		 */
		size: {
			default: 'md',
			schema: {type: 'string', enum: ['sm', 'md', 'lg', 'xl']}
		},
		/**
		 * Whether the icon spins. For example, set this property to true
		 * when used with the `loader-fill` icon.
		 * @type {boolean}
		 */
		spin: {default: false, schema: {type: 'boolean'}}
	},
	view,
	styles
});
