import {
	getDimensions,
	getContentDimensions,
	cacheDimensions
} from './dimensions';
import normalizePosition from './normalize-position';
import calculateFit from './calculate-fit';

/**
 * Calculates the fit styles for the content with only one possible position.
 * @param {Object} options
 * @param {HTMLElement} options.target - Reference to element the content is positioned beside
 * @param {HTMLElement} options.content - Reference to positioned element
 * @param {HTMLElement} [options.container=window] - Reference to element content should fit within
 * @param {string} [options.position="bottom-left"] - Desired position of content relative to target, e.g. "bottom-left" or "top-left bottom-right"
 * @param {number[]} [options.offset=[0,0]] - Offset content from the target in format `[vertical, horizontal]`, e.g. `[10, 5]`
 * @param {{height: number, width: number, minHeight: number, maxHeight: number, minWidth: number, maxWidth: number}} constrain - Constrains or sets the size content, each value can be a string
 * @param {boolean} [options.canScrollVertical=false] - Enable if the content displays a scrollbar on vertical overflow - the scrollbar will be added to calculations to avoid horizontal truncation
 * @param {boolean} [options.canScrollHorizontal=false] - Enable if the content displays a scrollbar on horizontal overflow - the scrollbar will be added to calculations to avoid vertical truncation
 * @return {FitInfo}
 */
export function getFitInfo({
	target,
	content,
	container = window,
	position = 'bottom-left',
	offset = [0, 0, 0, 0, 0, 0],
	constrain = {},
	canScrollVertical = false,
	canScrollHorizontal = false
}) {
	const targetDimensions = getDimensions(target);
	const contentDimensions = getContentDimensions(
		getDimensions(content),
		targetDimensions,
		constrain,
		canScrollVertical,
		canScrollHorizontal
	);
	const containerDimensions = getDimensions(container);
	const [targetPosition, contentPosition] = normalizePosition(position);
	const fit = calculateFit(
		offset,
		targetPosition,
		contentPosition,
		targetDimensions,
		contentDimensions,
		containerDimensions
	);
	const style = {
		top: fit.dimensions.top + 'px',
		left: fit.dimensions.left + 'px',
		width: fit.dimensions.width + 'px',
		height: fit.dimensions.height + 'px'
	};
	const detail = {
		targetDimensions,
		contentDimensions,
		containerDimensions,
		targetPosition,
		contentPosition,
		position: `${targetPosition.join('-')} ${contentPosition.join('-')}`
	};
	return {fit, style, detail};
}

/**
 * Calculates the best fit styles for the content based on multiple possible
 * positions. Returns the fit info for the first position that completely fits.
 * If no position completely fits, returns the position that fits the most.
 * @param {Object} options
 * @param {HTMLElement} options.target - Reference to element the content is positioned beside
 * @param {HTMLElement} options.content - Reference to positioned element
 * @param {HTMLElement} [options.container=window] - Reference to element content should fit within
 * @param {string[]} [options.position=["bottom-left"]] - Desired positions of content relative to target in order of preferences, e.g. ["bottom-left top-left", "top-left bottom-left"]
 * @param {number[]} [options.offset=[0,0]] - Offset content from the target in format `[vertical, horizontal]`, e.g. `[10, 5]`
 * @param {{height: number, width: number, minHeight: number, maxHeight: number, minWidth: number, maxWidth: number}} constrain - Constrains or sets the size content, each value can be a string
 * @param {boolean} [options.canScrollVertical=false] - Enable if the content displays a scrollbar on vertical overflow - the scrollbar will be added to calculations to avoid horizontal truncation
 * @param {boolean} [options.canScrollHorizontal=false] - Enable if the content displays a scrollbar on horizontal overflow - the scrollbar will be added to calculations to avoid vertical truncation
 * @return {FitInfo}
 */
export function getBestFitInfo({
	target,
	content,
	container = window,
	position = ['bottom-left'],
	offset = [0, 0, 0, 0, 0, 0],
	constrain = {},
	canScrollVertical,
	canScrollHorizontal
}) {
	const targetDimensions = cacheDimensions(getDimensions(target));
	const contentDimensions = cacheDimensions(getDimensions(content));
	const containerDimensions = cacheDimensions(getDimensions(container));
	let candidates = [];
	for (let pos of position) {
		const info = getFitInfo({
			target: targetDimensions,
			content: contentDimensions,
			container: containerDimensions,
			position: pos,
			offset,
			constrain,
			canScrollVertical,
			canScrollHorizontal
		});
		if (info.fit.overset.ratio === 0) {
			/* Fits completely, bail out and return */
			return info;
		}
		candidates.push(info);
	}
	/* Sort to find lowest overset ratio, aka the one that fits the most content */
	candidates.sort((a, b) => a.fit.overset.ratio - b.fit.overset.ratio);
	return candidates[0];
}
