import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-loader.scss';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import focusBehavior from '@servicenow/behavior-focus';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const renderLabel = (label) => {
	if (label) {
		return (
			<span className="now-loader-label">
				<span className="now-line-height-crop">{label}</span>
			</span>
		);
	}
};

const renderActionButton = (action, dispatch) => {
	if (action) {
		return (
			<now-button
				className="now-m-block-start--xl"
				label={action}
				variant="secondary"
				on-click={() => {
					dispatch(() => {
						return {
							type: 'NOW_LOADER#ACTION_CLICKED',
							payload: {action}
						};
					});
				}}
			/>
		);
	}
};

const view = (state, {dispatch}) => {
	const {label, action, size} = state.properties;

	return (
		<div className="now-loader">
			<now-icon
				className="now-loader-icon"
				icon="loader-fill"
				size={size}
				spin
			/>
			{renderLabel(label)}
			{renderActionButton(action, dispatch)}
		</div>
	);
};

/**
 * Feedback is important for the user experience when a user carries out an
 * action or is transitioning into a new page view. Feedback helps keep the user
 * engaged with their task and indicates that their actions are being carried out.
 * Loaders are stand-in elements that indicate a nondescript loading status to users.
 * Use loaders to facilitate the transition and retrieval of data between states and pages,
 * especially in cases where the timing can be variable.
 *
 * ```jsx
 * <now-loader />
 *
 * <now-loader label="Loading" action="Cancel"></now-loader>
 * ```
 *
 * @seismicElement now-loader
 * @summary An animated spinner to indicate a loading state
 */
createEnhancedElement('now-loader', {
	properties: {
		/**
		 * A simple description of the current loading activity.
		 * @type {string}
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Sets the loader size, either medium (md) or large (lg).
		 * @type {('md'|'lg')}
		 */
		size: {default: 'lg', schema: {type: 'string', enum: ['md', 'lg']}},
		/**
		 * The name of an action to display inside a simple button, placed under the loading indicator.
		 * @type {string}
		 */
		action: {schema: {type: 'string'}}
	},
	view,
	styles,
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	dispatches: {
		/**
		 * Dispatched when the user clicks on an action button in the loader.
		 * @type {{action: string}}
		 */
		'NOW_LOADER#ACTION_CLICKED': {}
	}
});
