import {findTabbableNodes} from '@servicenow/library-enhanced-utils';

/**
 * Dispatch an action to Seismic.
 *
 * @callback dispatchCallback
 * @param {object} coeffects
 */

/**
 * Synchronously focus the first node inside a host element.
 * @param {HTMLElement} host
 */
export const focusFirstNodeSync = (host) => {
	let focusableNodes = findTabbableNodes(host);

	if (focusableNodes.length) {
		return focusableNodes[0].focus();
	}

	// If we don't get focusable nodes, wait up to 5s,
	// check every 100ms until we find a node.

	const MAX_TRIES = 50;
	let count = 0;

	// Sadly the shadow root nodes aren't always rendered
	// by the time the modal is opened, so
	// we need to wait until they become available.
	const interval = setInterval(() => {
		focusableNodes = findTabbableNodes(host);

		if (focusableNodes.length) {
			focusableNodes[0].focus();
			return clearInterval(interval);
		}

		if (count === MAX_TRIES) {
			return clearInterval(interval);
		}

		count++;
	}, 100);
};

/**
 * Schedules `focusFirstNodeSync` async via action handler. Useful to get
 * access to `hoistHost` in action metadata.
 * @param {dispatchCallback} dispatch
 */
export const focusFirstNode = (dispatch) => {
	dispatch(() => ({type: 'PRIVATE#FOCUS_BEHAVIOR_FOCUS_FIRST_NODE'}));
};

/**
 * Decides on which first node focus call to make based on `host.nowHoist`.
 * @param {HTMLElement} host
 * @param {dispatchCallback} dispatch
 */
export const handleFocusFirstNode = (host, dispatch) => {
	return host.nowHoist || host.hoist
		? focusFirstNode(dispatch)
		: focusFirstNodeSync(host);
};
