import {createCustomElement} from '@servicenow/ui-core';
import styles from './_now-collapse.scss';

const view = (state) => {
	const {expanded} = state.properties;
	return (
		<div class={{'now-collapse': true, 'is-expanded': expanded}}>
			<slot />
		</div>
	);
};

/**
 * A helper element for expandable / collapsible sections.
 * ```jsx
 * <now-collapse expanded>
 *   Rich content area to be expanded and collapsed.
 * </now-collapse>
 * ```
 *
 * @seismicElement now-collapse
 * @summary A helper element for expandable / collapsible sections.
 */
createCustomElement('now-collapse', {
	properties: {
		/**
		 * Whether the collapsible section is currently expanded.
		 * @type {boolean}
		 */
		expanded: {default: false, schema: {type: 'boolean'}},
		/**
		 * Whether the collapsible section is visible to a screen reader.
		 * It is only visible to a screen reader when expanded.
		 * @type {string}
		 */
		ariaHidden: {
			computed: (state) => (state.properties.expanded ? 'false' : 'true'),
			reflect: true,
			schema: {type: 'string', enum: ['true', 'false']}
		}
	},
	slots: {
		/**
		 * Content placed in the default slot will appear inside of the collapsible section.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	styles,
	view
});
