/**
 * Calculates either the top or left offset based on the target and content
 * origins and dimensions.
 * @param {("block"|"inline")} axis - Axis that is being calculated (block for top/bottom, inline for left/right)
 * @param {("top"|"start"|"center"|"bottom"|"end")} targetOrigin - Logical target origin
 * @param {("top"|"start"|"center"|"bottom"|"end")} contentOrigin - Logical content origin
 * @param {Dimensions} targetDimensions - Target dimensions object
 * @param {Dimensions} overlayDimensions - Content dimensions object
 * @return {number}
 */
export function calculateSingleOffset(
	axis,
	targetOrigin,
	contentOrigin,
	targetDimensions,
	overlayDimensions
) {
	let offset = 0;
	/* First use the target origin property */
	if (targetOrigin === 'top' || targetOrigin === 'start') {
		offset += axis === 'block' ? targetDimensions.top : targetDimensions.left;
	}
	if (targetOrigin === 'center') {
		offset +=
			axis === 'block'
				? targetDimensions.top + targetDimensions.height / 2
				: targetDimensions.left + targetDimensions.width / 2;
	}
	if (targetOrigin === 'bottom' || targetOrigin === 'end') {
		offset +=
			axis === 'block'
				? targetDimensions.top + targetDimensions.height
				: targetDimensions.left + targetDimensions.width;
	}
	/* Then use the content origin property */
	if (contentOrigin === 'top' || contentOrigin === 'start') {
		offset -= 0;
	}
	if (contentOrigin === 'center') {
		offset -=
			axis === 'block'
				? overlayDimensions.height / 2
				: overlayDimensions.width / 2;
	}
	if (contentOrigin === 'bottom' || contentOrigin === 'end') {
		offset -=
			axis === 'block' ? overlayDimensions.height : overlayDimensions.width;
	}
	return offset;
}

/**
 * Calculates the ratio of the content that is overset from its container.
 * Returns the ratio as a whole number, e.g. `75` if 75% of the content does not
 * fit inside of the container.
 * @param {number} top
 * @param {number} right
 * @param {number} bottom
 * @param {number} left
 * @param {("top"|"bottom"|"center")} block - direction to constrain block from
 * @param {("start"|"end"|"center")} inline - direction to constrain inline from
 * @param {Dimensions} contentDimensions
 * @param {Dimensions} containerDimensions
 * @return {{dimensions: Dimensions, overset: {vertical: number, horizontal: number, ratio: number}}}
 */
export function constrainOffset(
	top,
	right,
	bottom,
	left,
	block,
	inline,
	contentDimensions,
	containerDimensions
) {
	const toff =
		top < containerDimensions.top
			? Math.abs(Math.abs(containerDimensions.top) - top)
			: 0;
	const boff =
		bottom > containerDimensions.bottom
			? Math.abs(Math.abs(bottom) - Math.abs(containerDimensions.bottom))
			: 0;
	const loff =
		left < containerDimensions.left
			? Math.abs(Math.abs(containerDimensions.left) - left)
			: 0;
	const roff =
		right > containerDimensions.right
			? Math.abs(Math.abs(right) - Math.abs(containerDimensions.right))
			: 0;
	if (toff === 0 && boff === 0 && loff === 0 && roff === 0) {
		return {
			dimensions: {...contentDimensions, top, left, bottom, right},
			overset: {vertical: 0, horizontal: 0, ratio: 0}
		};
	}
	const dimensions = {...contentDimensions, top, left, bottom, right};
	if (block === 'top' && boff > 0) {
		dimensions.top = top;
		dimensions.bottom = containerDimensions.bottom;
		dimensions.height = contentDimensions.height - boff;
	}
	if (block === 'bottom' && toff > 0) {
		dimensions.top = containerDimensions.top;
		dimensions.bottom = bottom;
		dimensions.height = contentDimensions.height - toff;
	}

	if (block === 'center' && (toff > 0 || boff > 0)) {
		const largest = Math.max(toff, boff);
		dimensions.top = top + largest;
		dimensions.bottom = bottom - largest;
		dimensions.height = contentDimensions.height - largest * 2;
	}

	if (inline === 'start' && roff > 0) {
		dimensions.left = left;
		dimensions.right = containerDimensions.right;
		dimensions.width = contentDimensions.width - roff;
	}
	if (inline === 'end' && loff > 0) {
		dimensions.left = containerDimensions.left;
		dimensions.right = right;
		dimensions.width = contentDimensions.width - loff;
	}
	if (inline === 'center' && (loff > 0 || roff > 0)) {
		const largest = Math.max(loff, roff);
		dimensions.left = left + largest;
		dimensions.right = right - largest;
		dimensions.width = contentDimensions.width - largest * 2;
	}
	const ratio =
		100 -
		Math.round(
			((dimensions.height * dimensions.width) /
				(contentDimensions.height * contentDimensions.width)) *
				100
		);
	const overset = {
		vertical: contentDimensions.height - dimensions.height,
		horizontal: contentDimensions.width - dimensions.width,
		ratio
	};
	return {dimensions, overset};
}

const addOffsetPaddingToDimensions = (dim, offset) => {
	const [, , t = 0, r = 0, b = 0, l = 0] = offset;
	return {
		...dim,
		top: dim.top - t,
		right: dim.right + r,
		bottom: dim.bottom + b,
		left: dim.left - l,
		height: dim.height + t + b,
		width: dim.width + l + r
	};
};

/**
 * Calculates the dimensions for the content after it is positioned against
 * to the target and constrained to fit within the container.
 * @param {number[]} offset - Offset content from the target in format `[vertical, horizontal]`, e.g. `[10, 5]`
 * @param {Origin[]} targetPosition
 * @param {Origin[]} contentPosition
 * @param {Dimensions} targetDimensions
 * @param {Dimensions} contentDimensions
 * @param {Dimensions} containerDimensions
 * @return {{fromWindow: Dimensions, fromContainer: Dimensions, dimensions: Dimensions, overset: {vertical: number, horizontal: number, ratio: number}}}
 */
export default function calculateFit(
	offset,
	targetPosition,
	contentPosition,
	targetDimensions,
	contentDimensions,
	containerDimensions
) {
	const [targetOriginBlock, targetOriginInline] = targetPosition;
	const [contentOriginBlock, contentOriginInline] = contentPosition;
	const paddedTargetDimensions = addOffsetPaddingToDimensions(
		targetDimensions,
		offset
	);

	const top =
		calculateSingleOffset(
			'block',
			targetOriginBlock,
			contentOriginBlock,
			paddedTargetDimensions,
			contentDimensions
		) + offset[0];
	const left =
		calculateSingleOffset(
			'inline',
			targetOriginInline,
			contentOriginInline,
			paddedTargetDimensions,
			contentDimensions
		) + offset[1];
	const bottom = top + contentDimensions.height;
	const right = left + contentDimensions.width;
	const {dimensions, overset} = constrainOffset(
		top,
		right,
		bottom,
		left,
		contentOriginBlock,
		contentOriginInline,
		contentDimensions,
		containerDimensions
	);
	return {
		fromWindow: {
			...dimensions
		},
		fromContainer: {
			...dimensions,
			top: dimensions.top - containerDimensions.top,
			right: dimensions.right - containerDimensions.left,
			left: dimensions.left - containerDimensions.left,
			bottom: dimensions.bottom - containerDimensions.top
		},
		dimensions,
		overset
	};
}
